import RSSParser from 'rss-parser';
import { Members, CORS_PROXY } from 'src/constants';
import { flattenDepth } from 'lodash';
import { Member, BlogFeed } from 'src/models';

export async function fetchMemberFeeds(member: Member): Promise<BlogFeed[]> {
  if (!member.rss) {
    return [];
  }

  const parser = new RSSParser();
  const response = await parser.parseURL(`${CORS_PROXY}${member.rss}`);
  if (!response.items) {
    response.items = [];
  }

  const feeds: BlogFeed[] = response.items.map(item => {
    return {
      author: member,
      title: item.title ?? '',
      contentSnippet: item.contentSnippet ?? '',
      pubDate: item.pubDate ?? '',
      link: item.link ?? '',
    };
  });

  return feeds;
}

export async function fetchAllFeeds(): Promise<BlogFeed[]> {
  const jobs = Members.filter(member => member.rss).map(member => fetchMemberFeeds(member));
  const response = await Promise.all(jobs);

  return flattenDepth(response, 2).sort((a, b) => new Date(b.pubDate).getTime() - new Date(a.pubDate).getTime());
}
