/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { Link } from 'gatsby';
import { Row, Col } from 'react-bootstrap';
import { GlobalMenus, Color } from 'src/constants';
import { Media } from 'src/utils';
import LogoSvg from 'src/images/logos/logo.svg';
import LogoWhitePng from 'src/images/logos/logo_white.png';

interface Props {
  logoColor: Color;
}
const Header = ({ logoColor = Color.Black }) => {
  const logoImage = logoColor === Color.Black ? LogoSvg : LogoWhitePng;
  return (
    <header
      css={css`
        padding: 1rem;
      `}
    >
      <Row>
        <Col xs="auto">
          <Link
            css={css`
              display: flex;
              align-items: center;
              &:hover {
                text-decoration: none;
              }
            `}
            to="/"
          >
            <Logo src={logoImage} alt="Lubycon " />
            <LubyconName>LUBYCON</LubyconName>
          </Link>
        </Col>
        <Col />
        <Col xs="auto">
          <Menus>
            {GlobalMenus.map((menu, index) => {
              return (
                <Menu key={index}>
                  {menu.externalLink ? (
                    <a href={menu.link} target="_blank" rel="noopener noreferrer">
                      {menu.name}
                    </a>
                  ) : (
                    <Link to={menu.link}>{menu.name}</Link>
                  )}
                </Menu>
              );
            })}
          </Menus>
        </Col>
      </Row>
    </header>
  );
};

const Logo = styled.img`
  width: 30px;
  margin: 0 0.5rem 0 0;
  vertical-align: middle;
`;
const LubyconName = styled.span`
  color: ${Color.Black};
  font-size: 1.3rem;
  ${Media.mobile} {
    display: none;
  }
`;
const Menus = styled.ul`
  display: flex;
  padding: 0;
  margin: 0;
  align-items: center;
  height: 100%;
`;
const Menu = styled.li`
  font-size: 1rem;
  list-style: none;
  margin-right: 1.5rem;
  ${Media.mobile} {
    margin-right: 0.6rem;
  }
  a {
    color: ${Color.Grey900};
    text-decoration: none;
    transition: color 0.2s ease-in;
    &:hover {
      color: ${Color.Red500};
    }
    ${Media.mobile} {
      font-size: 0.7rem;
    }
  }
`;

export default Header;
